import { drawLegalMoves, calculateMove, calculateWinner } from './moves.js';

export function calculateWeight(currentVal, player, depth) {
    const w1 = 100; // Pieces (1)
    const w2 = 10; // Moves (100)
    const w3 = 1000000; // checkmate
    const w4 = 5; // attack (10)
    return w1 * calculateWeight1(currentVal, player, depth) +
        w2 * calculateWeight2(currentVal, player, depth) +
        w3 * calculateWeight3(currentVal, player, depth) +
        w4 * calculateWeight4(currentVal, player, depth);
}

function calculateWeight1(currentVal, player, depth) {
    let weight = 0;
    let friendly;
    let enemy;
    if (player) {
        friendly = /[PTCADR]/;
        enemy = /[ptcadr]/;
    } else {
        friendly = /[ptcadr]/;
        enemy = /[PTCADR]/;
    }
    if (depth === 0) {
        for (let i = 0; i < 64; i++) {
            if (friendly.test(currentVal[i])) {
                weight += pointPiece(currentVal[i]);
            } else if (enemy.test(currentVal[i])) {
                weight -= pointPiece(currentVal[i]);
            }
        }
    } else if (depth === 1) {
        let m1 = [];
        let m2 = [];
        let w1 = [];
        for (let i = 0; i < 64; i++) {
            if (enemy.test(currentVal[i])) {
                const legalMoves = drawLegalMoves(i, currentVal);
                for (let j = 0; j < legalMoves.length; j++) {
                    const piece = currentVal[i];
                    const currentVal1 = calculateMove(piece, i, legalMoves[j], currentVal);
                    const weight = calculateWeight(currentVal1, !player, depth - 1);
                    m1 = m1.concat([i]);
                    m2 = m2.concat([legalMoves[j]]);
                    w1 = w1.concat([weight]);
                }
            }
        }
        const moves = m1.length;
        const maxWeight = Math.max.apply(Math, w1);
        let bM = [];
        for (let i = 0; i < moves; i++) {
            if (w1[i] === maxWeight) {
                bM = bM.concat([i]);
            }
        }
        const numBestMoves = bM.length;
        const chosenMove = bM[Math.floor(Math.random() * numBestMoves)];
        const move1 = m1[chosenMove];
        const move2 = m2[chosenMove];
        const piece = currentVal[move1];
        const currentVal1 = calculateMove(piece, move1, move2, currentVal);
        weight = calculateWeight(currentVal1, player, depth - 1);
    }
    return weight;
}
function pointPiece(piece) {
    if (/[Pp]/.test(piece)) {
        return 1;
    } else if (/[Tt]/.test(piece)) {
        return 5;
    } else if (/[Cc]/.test(piece)) {
        return 3;
    } else if (/[Aa]/.test(piece)) {
        return 3;
    } else if (/[Dd]/.test(piece)) {
        return 10;
    } else if (/[Rr]/.test(piece)) {
        return 20;
    }
}
function calculateWeight2(currentVal, player, depth) {
    let weight = 0;
    let friendly;
    if (player) {
        friendly = /[PTCADR]/;
    } else {
        friendly = /[ptcadr]/;
    }
    for (let i = 0; i < 64; i++) {
        if (friendly.test(currentVal[i])) {
            weight += pointPiece(currentVal[i]) * drawLegalMoves(i, currentVal).length;
        }
    }
    return weight/100;
}

function calculateWeight3(currentVal, player, depth) {
    let winner;
    if (player) {
        winner = "Blancas";
    } else {
        winner = "Negras";
    }
    if (calculateWinner(currentVal, player) === winner) {
        return 1;
    }
    return 0;
}

function calculateWeight4(currentVal, player, depth) {
    let weight = 0;
    let friendly;
    let enemy;
    if (player) {
        friendly = /[PTCADR]/;
        enemy = /[ptcadR]/;
    } else {
        friendly = /[ptcadr]/;
        enemy = /[PTCADR]/;
    }
    for (let i = 0; i < 64; i++) {
        if (friendly.test(currentVal[i])) {
            const legalMoves = drawLegalMoves(i, currentVal);
            for (let j = 0; j < 64; j++) {
                if (enemy.test(currentVal[j]) && legalMoves.includes(j)) {
                    weight += pointPiece(currentVal[j]);
                }
            }
        }
    }
    return weight/10;
}



